
.wrapperPage{
    margin-left: 15px;
    display: flex;
    column-gap: 20px;
    flex-wrap: wrap;
}

.btn{
    width: 90px;
    display: flex;
    justify-content: center;
    padding: 14px 0;
    margin: 0 0 10px 10px;
    border-radius: 6px;
    background-color: rgba(102, 196, 196, 0.408);
    color: rgba(0, 0, 0, 0.65);
    cursor: pointer;
}

.additional{
    margin-top: 20px;
    margin-left: 15px;
    font-size: 20px;
}

.cast{
    margin-bottom: 14px;
}

.wrapperImg{
    min-width: 280px;
    max-width: 420px;
    min-height:340px ;
    max-height: 630px;
}

.img{
    width: 100%;
    display: block;
}

.textOverview{
width: auto;
    max-width: 700px;
}

